*{
  margin: 0;
}

.App{
  background: #eee;
  width: 100%;
  height: auto;
  align-items: center;
  justify-content: center;
}

.navbar-custom{
  background-color: cornflowerblue;
}

.navbar-link:hover{
  color: white;
  text-decoration: none;
}

.navbar-link{
  color: #eee;
  text-decoration: none;
}

.container{
  width: 70%;
  background: blue;
  padding: 0;
  position: relative;
  right: 0px;
}

.header{
  text-align: center;
  background: white;
  align-content: center;
  font-size: 8vh;
  width: 100%
}

.products-container{
  display: flex;
  flex-direction: row;
  background: white;
  width: 100%;
  flex-wrap: wrap;
  padding-left: 7%;
  padding-right: auto;
  padding-top: 2vh;
  padding-bottom: 5vh;
  margin: 0;
}

.product-card{
  width: 30%;
  height: auto;
  background: white;
  box-shadow: 0px 5px 10px #888;
  border-radius: 5px;
  margin-right: 5px;
  margin-left: 5px;
  margin-top: 10px;
}

.footer{
  bottom: 0;
  left: 0;
  display:flex;
  flex-direction: row;
  width: 100%;
  height: auto;
  background:cornflowerblue;
  color: white;
  padding: 2%;
}

.inputsearch{
 position: absolute;
 right: 0;
 width: 20%;
 margin-right: 10%;
}

.filter-container{
  width: 100%;
  background: white;
  padding: 2%;
  overflow-y: auto;
}

.list{
  list-style: none;
  cursor: pointer;
  text-decoration: underline;
  margin-right: 50px;
  width: 100%;
}

.mainpanel{
  display: flex;
  flex-direction: row;
  background: #eee;
  padding-left: 10px;
}

.filter{
  background: white;
  width: 20%;
  padding: 2%;
}

.filter-header{
  width: 100%;
  background: cornflowerblue;
  padding: 2%;
  color: white;
  border-radius: 5px;
  margin-bottom: 10px;
}
.filter-title{
  background: #ccc;
  padding: 2%;
  border-radius: 5px;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
}


@media (max-width: 550px){
  .container{
    width: 100%;
    height: auto;
    position: relative;
    left: 0%;
    transform: translateX(-0%);
  }
  .products-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding-left: 1vw;
    padding-right: 1vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }
  .product-card{
    width: 100%;
    height: auto;
    background: white;
    margin-left: 2vh;
  }
  .header{
    font-size: 5vh;
    width: 100%
  }
  .footer{
    flex-direction: column;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
  }
  .inputsearch{
    position: relative;
    width: 90%;
    margin-right: 0;
   }
  .filter-container{
    display: flex;
    flex-direction: row;
    width: 100%;
    background: white;
    padding: 2%;
  }
  .mainpanel{
    padding-left: 0;
    flex-direction: column;
  }
  .filter{
    width: 100%;
  }

  .filter-header::after{
    content:'>';
    font-weight: bold;
    position: relative;
    left: 68%;
  }

  .hide{
    display: none;
  }

  .lefilter{
    display: flex;
    flex-direction: row;
  }

  .list{
    text-decoration: none;
  }
}